#panel{
    width: 50vw;
    max-width: 524px;
    height: 100vh;
    background-image: url("/public/img/white_noise.png");
    background-size: 160px 160px;
    backdrop-filter: blur(20px);
    background-color: rgb(0 0 0 / 23%);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: fixed;
    /* top: 60px; */
    bottom: 0;
    z-index: 100;
    right: -100vh;
    transition: all 0.8s;
    padding-top: 60px;
    box-shadow: 1px 1px 7px 3px #1e1e1e7d;
}


#panel.open {
    right: 0;
    transition: 0.4s cubic-bezier(0, 0.54, 0, 1.03) right;

