.presentation{
    background-image: linear-gradient(#0b0c10, #151515);
    background-color: black;
    background-attachment: fixed;
    /*width: 100vw; TODO RE-ENABLE*/
    height: 70vh;
    background-repeat: round;
    background-size: cover;
    position: relative;
    z-index: 4;
}

.presentation h2{
    font-family: var(--font-family);
    font-weight: bold;
    font-style: var(--font-style);
    font-size: 62px;
    text-align: left;
    font-feature-settings: "pcap", "c2pc";
    font-variant: all-petite-caps;
    letter-spacing: -2px;
    line-height: 50px;
    max-width: 100%;
    width: 100%;
    display: flex;
    vertical-align: middle;
    margin: 0;
    float: inherit;
    color: transparent;
    background-image:  linear-gradient(142deg, #d2d5d8, #52505d);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    /*filter: invert();*/
    /* background-attachment: fixed; */
    background-size: cover;
    background-repeat: round;

    animation-duration: 3s;
    animation-name: fadeIn;
    /*animation-iteration-count: infinite;*/
    /*animation-direction: alternate;*/
    /*animation-delay: 2s;*/
    /*opacity: 0;*/
}

.presentation p {
    color: #c9c9c9;
    letter-spacing: 1px;
    margin-top: 5px;
    padding-left: 3.5px;
}

.profilePic{
    height: 310px;
    width: 310px;
    border-radius: 180px;
    border : 3px solid var(--color_allSkill);
    display: flex;
    justify-content: center;
    align-items: center;
}

.profilePic img{
    height: 300px;
    width: 300px;
    border-radius: 180px;
    backdrop-filter:   invert() blur(5px) hue-rotate(94deg) brightness(0.9);
    filter: drop-shadow(0px 0px 10px var(--brown2));
    /*opacity: 0.5%;*/
}

#profile{
    display: flex;
    justify-content: center;

    height: 500px;
    margin: auto;
    padding-top: 200px;
}

.titles{
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-wrap: wrap;
    padding-left:20px;
}

.bubble{
    display: flex;
    width: 60px;
    height: 60px;
    background-color: rgba(255, 255, 255, 0.24);
    backdrop-filter: blur(10px) saturate(180%);
    border-radius: 100%;
    background-image: url("/public/img/icons/down-arrow.png");
    /*background-size: contain;*/
    margin: auto;
    transition: scale 0.2s;
    animation-duration: 2s;
    animation-name: arrowBounce;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-delay: 2s;
    box-shadow: 0 0 10px -4px #7f7f7f, 0px 1px 12px -8px inset white, 0px -3px 12px 0px inset black;
    background-size: 25px 25px;
    background-repeat: no-repeat;
    background-position: center 20px;
    color: transparent;
    text-decoration: none;

    &:hover{
        scale: 120%;
        transition: scale cubic-bezier(0.51, -0.64, 0.28, 1.63) 0.2s;
    }

}



@keyframes arrowBounce {
    0% {
        transform: translateY(0px);
    }
    100% {
        transform: translateY(50px);
    }
}

@keyframes fadeIn {
    0% {
        opacity : 0;
    }
    100% {
        opacity : 1;
    }
}
